// 
export const upload_file = (key,path,rqd,backfn)=>{
    return {
        action:host + path,
        accept:"image/*",
        listType:"picture-card",
        data:rqd,
        headers:{
            "X-Requested-With": null
        },
        multiple:true,
        name:key,
        withCredentials:true,
        onChange:function(data){
            backfn(data,rqd)
        }
    }
}



