import React , { Component } from 'react';
import { Row , Table } from 'antd';

import TableCol from '../../../utils/table-col';
import axios from '../../../ajax/request_form';
import { gtask_detail , gtask_img_url } from '../../../ajax/api';
import { host } from '../../../ajax/config';
import { format_date } from '../../../ajax/tool';

class Detail extends Component {
    constructor(props){
        super(props);
        this.state = {
            orderId:props.orderNo,
            repay_info:{
                loanMoney:"",
                downPayment:0,
                productName:"--",
                periods:"",
                syx:"",//
                discountRate:""
            },
            repay_plan:[
                
            ],
            base_info_data:{

            },
            borrowerType:''
        }
    }
    componentWillMount () {
        this.base_info_company = {
            beneficiary:{
                name:"",
                width_key:"10%"
            },
            cardNo:{
                name:"",
                width:"10%"
            },
            phone:{
                name:""
            },
            engineNo:{
                name:""
            },
            ownerName:{
                name:""
            },
            plateNo:{
                name:""
            },
            vinNo:{
                span_val:3,
                name:""
            },
            // xcfpj:{
            //     name:""
            // },
            tbr:{
                name:"",
            },
            agent_name:{name:""},
            agent_phone:{name:""},
            agent_idCard:{name:""},
            simpleStorages:{
                name:"",
                span_val:7,
                className_val:"text-left",
                render:(data)=>{
                    return data.simpleStorages
                }
            }
        }
        this.base_info = {
            beneficiary:{
                name:"",
                width_key:"10%"
            },
            cardNo:{
                name:"",
                width:"10%"
            },
            phone:{
                name:""
            },
            engineNo:{
                name:""
            },
            ownerName:{
                name:""
            },
            plateNo:{
                name:"",
                render:(data)=>{
                    return data.plateNo===null?'':data.plateNo
                }
            },
            vinNo:{
                span_val:3,
                name:""
            },
            // xcfpj:{
            //     name:""
            // },
            tbr:{
                name:"",
                span_val:7
            },
            simpleStorages:{
                name:"",
                span_val:7,
                className_val:"text-left",
                render:(data)=>{
                    return data.simpleStorages
                }
            }
        }
        this.bill_info = {
            insurCompany:{
                name:"",
                width_key:"12%",
                width_val:"13%"
            },
            syx:{
                name:"()",
                width_key:"13%",
                width_val:"15%",
                render:(data)=>{
                    return data.syx.money()
                }
                
            },
            receipt:{
                name:"",
                width_key:"15%",
                span_val:3
            },
            bdNo:{
                name:"",
                span_val:3,
            },
            syxze:{
                name:"",
                span_val:3,
                render:(data)=>{
                    return data.startDate+""+data.endDate
                }
            }
        }
        this.fjx_info = {
            insurCompany:{
                name:"",
                width_key:"12%",
                width_val:"13%"
            },
            syfjx:{
                name:"()",
                width_key:"13%",
                width_val:"15%",
                render:(data)=>{
                    return data.syfjx.money()
                }

            },
            receipt:{
                name:"",
                width_key:"15%",
                span_val:3
            },
            bdNo:{
                name:"",
                span_val:3,
            },
            syxze:{
                name:"",
                span_val:3,
                render:(data)=>{
                    return data.startDate+""+data.endDate
                }
            }
        }
        this.jqx_info = {
            insurCompany:{
                name:"",
                width_key:"12%",
                width_val:"13%"
            },
            jqx:{
                name:"()",
                width_key:"13%",
                width_val:"13%",
                render:(data)=>{
                    return data.jqx!==null?data.jqx.money():''
                }

            },
            ccs:{
                name:"()",
                width_key:"13%",
                width_val:"17%",
                render:(data)=>{
                    return data.ccs!==null?data.ccs.money():''
                }

            },
            receipt:{
                name:"",
                //width_key:"15%",
                //span_val:3
            },
            bdNo:{
                name:"",
                //span_val:3,
            },
            syxze:{
                name:"",
                //span_val:3,
                render:(data)=>{
                    return data.startDate+""+data.endDate
                }
            }
        }
        let pay_status = {
            "-1":"--",
            "0":"",
            "1":"",
            "2":"",
            "3":"",
        }
        this.columns = [
            {
                title:"",
                dataIndex:"period"
            },
            {
                title:"",
                render:(data)=>{
                    return format_date(data.repayDate)
                }
            },
            {
                title:"",
                render:(data)=>{
                    return format_date(data.realRepayDate)||"--"
                }
            },
            {
                title:"",
                render:(data)=>{
                    return data.repayMoney.money()
                }
            },
            {
                title:"",
                render:(data)=>{
                    return data.realRepayMoney.money()
                }
            },
            {
                title:"",
                render:(data)=>{
                    return data.lateFee.money()
                }
            },
            {
                title:"",
                render:(data)=>{
                    return pay_status[data.status]||"--"
                }
            }
        ]
    }
    componentDidMount(){
        this.get_info();
    }
    get_info(){
        let rqd = {
            orderId : this.state.orderId
        }
        axios.post(gtask_detail,rqd).then(data=>{
            this.transfrom_data(data.data);
        })
    }
    transfrom_data(data){
        let res = {};
        if(data.borrowerType==0){
            res.base_info_data = {
                ...data.basicInfo,
            }
        }else{
            res.base_info_data = {
                ...data.basicInfo,
                agent_name:data.agentInfo.name,
                agent_phone:data.agentInfo.phone,
                agent_idCard:data.agentInfo.idCard,
            }
        }


        res.base_info_data.simpleStorages = [];
        for(let d in data.basicInfo.simpleStorages){
            let storage = data.basicInfo.simpleStorages[d];
            if(storage.storageNos.indexOf("[")>=0){
                let list = JSON.parse(storage.storageNos);
                for(let l in list){
                    res.base_info_data.simpleStorages.push(<img alt={d} key={d+l} src={host+gtask_img_url+"?storageNo="+list[l]} />)
                }
            }else{
                res.base_info_data.simpleStorages.push(<img alt={d} key={d} src={host+gtask_img_url+"?storageNo="+storage.storageNos} />)
            }
        }
        res.repay_info = {
            loanMoney:data.repayPlan.loanMoney,
            productName:data.repayPlan.productName,
            periods:data.repayPlan.periods,
            downPayment:data.repayPlan.downPayment,
            syx:data.repayPlan.syx,//
            discountRate:data.repayPlan.discountRate
        }
        res.repay_plan = data.repayPlan.repayPlanDetailList;
        res.bill_info_data = data.insurDetailItemList;
        res.borrowerType=data.borrowerType;
        this.setState({
            ...res
        })
    }
    render() {
        let table_props = {
            columns : this.columns,
            className : "table-sh",
            pagination : false,
            rowKey:"period",
            dataSource : this.state.repay_plan
        }
        let bills = [],bill_fjx=[],syx=[],fjx=[],bill_jqx=[],jqx=[];
        let datas = this.state.bill_info_data||[];
        datas.sort(function(a,b){
            if(a.ordinal>b.ordinal){
                return 1
            }else{
                return 0
            }
        });
        let tbdType={1:'',2:'',3:'|'};
        for(let b in datas){
            if(datas[b].type===1){
                bills.push(datas[b]);
            }else if(datas[b].type===2){
                bill_fjx.push(datas[b]);
            }else{
                bill_jqx.push(datas[b]);
            }
            //bills.push(
            //    <div key={b}>
            //        <div className="sub-title">{tbdType[datas[b].type]}</div>
            //        <div className="sub-title">{datas[b].ordinal}</div>
            //        <TableCol data-source = {datas[b]} data-columns={this.bill_info} />
            //    </div>
            //)
        }
        for(let i in bills){
            syx.push(<div key={i}>
                <div className="sub-title">{bills[i].ordinal}</div>
                <TableCol data-source = {bills[i]} data-columns={this.bill_info} />
            </div>)
        }
    for(let j in bill_fjx){
        fjx.push(<div key={j}>
            <div className="sub-title">{bill_fjx[j].ordinal}</div>
            <TableCol data-source = {bill_fjx[j]} data-columns={this.fjx_info} />
        </div>)
    }
    for(let j in bill_jqx){
        jqx.push(<div key={j}>
            <div className="sub-title">{bill_jqx[j].ordinal}</div>
            <TableCol data-source = {bill_jqx[j]} data-columns={this.jqx_info} data-row={6}/>
        </div>)
    }
        return (
            <span>
            <Row className = "content query-sh">
                <div className="title"><div className="icon" />{this.state.base_info_data.name}</div>
                <TableCol data-source = {this.state.base_info_data} data-columns={this.state.borrowerType==1?this.base_info_company:this.base_info} />
            </Row>

            <Row style={{display:bills.length>0?"block":"none"}} className = "content query-sh">
                <div className="title"><div className="icon" /></div>
                {syx.length>0?<div className="sub-title" style={{fontWeight:'bold',fontSize:'16px'}}></div>:''}
                {syx.length>0?syx:'' }
                {fjx.length>0?<div className="sub-title" style={{fontWeight:'bold',fontSize:'16px'}}></div>:''}
                {fjx.length>0?fjx:''}
                {jqx.length>0?<div className="sub-title" style={{fontWeight:'bold',fontSize:'16px'}}>|</div>:''}
                {jqx.length>0?jqx:''}
            </Row>

            <Row className = "content query-sh last">
                <div className="title"><div className="icon" />{this.state.repay_info.productName}
                    <span>{this.state.repay_info.periods}</span>
                </div>
                <table className="bmd-tableCol">
                    <tbody>
                        <tr>
                            <td className="key"></td>
                            <td>{this.state.repay_info.loanMoney.money()}</td>
                            <td className="key"></td>
                            <td>&emsp;{this.state.repay_info.periods}&emsp;</td>
                            <td className="key"></td>
                            <td>{this.state.repay_info.downPayment.money()}(*{this.state.repay_info.discountRate}%)</td>
                        </tr>
                    </tbody>
                </table>
                {
                    //<Table {...table_props} bordered />
                }

            </Row>
            </span>
        )
    }
}


export default Detail;


