import React , { Component } from 'react';
import { Row , Col , Table , Modal } from 'antd';
import { Link } from 'react-router';
import { browserHistory } from 'react-router';
import QRCode from 'qrcode.react';

// import { order_status_map } from '../../../ajax/config';
// import TableCol from './../../../utils/table-col';
import axios from '../../../ajax/request_form';
import { get_repayment_info , get_payment_plan , verify_repay_status } from '../../../ajax/api';

// const QRCode = require('qrcode.react');

class Audit extends Component {
    constructor(props){
        super(props);
        this.state = {
            downPayment:0,
            loanMoney:"",
            money:"",
            periods:"",
            repayDate:"",
            codeUrl:"",
            repayDay:"",
            success:false,
            visible:false
        }
    }
    componentWillMount () {
        let pay_status = {
            "-1":"--",
            "0":"",
            "1":"",
            "2":"",
            "3":"",
        }
        this.columns = [
            {
                title:"",
                dataIndex:"period"
            },
            {
                title:"",
                dataIndex:"repayDate"
            },
            {
                title:"",
                dataIndex:"realRepayDate",
                render:data=>{
                    return data||"--"
                }
            },
            {
                title:"()",
                render:(data)=>{
                    return data.repayMoney.money()
                }
            },
            {
                title:"()",
                render:(data)=>{
                    return data.realRepayMoney.money()
                }
            },
            {
                title:"()",
                render:(data)=>{
                    return data.lateFee.money()
                }
            },
            {
                title:"",
                render:(data)=>{
                    return pay_status[data.status]
                }
            }
        ]
        
    }
    componentDidMount(){
        this.get_info();
        this.get_plan();
    }
    componentWillUnmount(){
        clearInterval(this.timer);
    }
    // 
    get_info(filter={}){
        let rqd = {
            orderNo:this.props.location.query.orderNo
        }
        axios.post(get_repayment_info,rqd).then(data=>{
            let res = data.data;
            this.setState({
                ...res
            })
            this.timer = setInterval((e)=>{
                this.verify_state(res.requestId);
            },2000);
        })
    }
    get_plan(){
        let rqd = {
            orderId:this.props.location.query.orderId
        }
        axios.post(get_payment_plan,rqd).then(data=>{
            let res = data.data;
            this.setState({
                repayDay:res.repayDay,
                downPayment:res.downPayment,
                productName:res.productName,
                periods:res.periods,
                list:res.repayPlanDetailList
            })
        })
    }
    show(){
        this.setState({
            visible:true
        })
    }
    onOk(){

    }
    // 
    verify_state(requestId){
        let rqd = {
            requestId:requestId
        }
        axios.post(verify_repay_status,rqd).then(data=>{
            if(data.data){
                let auto_second = this.state.auto_second;
                clearInterval(this.timer);
                this.setState({
                    success:true
                });
                setInterval(e=>{
                    if(auto_second===0){
                        browserHistory.push("/fq/query");
                    }
                    this.setState({
                        auto_second:auto_second--
                    })
                    
                },1000);
            }
            
        })
    }
    onCancel(){
        this.setState({
            visible:false
        })
    }
    render() {
        let table_props = {
            columns : this.columns,
            pagination : false,
            rowKey:"orderId",
            dataSource : this.state.list
        }
        let modal_props = {
            title:"",
            visible:this.state.visible,
            width:"60%",
            className:"pay-modal modal-long",
            footer:null,
            onOk:this.onOk.bind(this),
            onCancel:this.onCancel.bind(this)
        }
        let modal_result = {
            title:"",
            visible:this.state.success,
            className:"pay-modal",
            footer:null
        }

        let qrimg = this.state.codeUrl?<QRCode size={150} value = {this.state.codeUrl} />:"";
        return (
            <Row className="content pay">
                <div className="pay-content">
                    <div className="page-title"></div>
                    <div className="moneys">
                        <span className="money">
                            <span>{this.state.money.money()}</span>
                        </span><br />
                    </div>
                    <div className="info">
                        {/*<span className="origin">{this.state.periods}</span> | {this.state.repayDay}&emsp;&emsp;&emsp;&emsp; */}
                        {/*<span className="origin">{this.state.loanMoney}</span>*/}
                        <span>{this.state.loanMoney.money()}</span><br />
                        <span> <span className="origin">{this.state.period}</span> <span className="origin">{this.state.repayDate}</span></span>
                        <a onClick={this.show.bind(this)}></a>
                    </div>
                </div>
                <div className="pay-content">
                    <div className="page-title"></div>
                    <Row>
                        <Col span={12}>
                            <Row>
                                <div className="page-subtitle"></div>
                                <Col span={12}>
                                    { qrimg }
                                    <div></div>
                                </Col>
                            </Row>
                        </Col>
                    </Row><br />
                    {/*<Row>
                        <div className="page-subtitle">2</div>
                        <Col span={12}>
                            <TableCol data-source = {test} data-columns={this.columns}/>
                        </Col>
                    </Row>
                    <br />
                    <div><span className="origin"></span> +</div>*/}
                </div>
                <Modal {...modal_props}>
                    <span className="money-item">{this.state.loanMoney.money()}</span>
                    <span className="money-item">{this.state.downPayment.money()}</span>
                    <span className="money-item">{this.state.repayDay}</span>
                    <br /><br />
                    <Table bordered {...table_props} />
                </Modal>
                <Modal {...modal_result}>
                    <span className="page-subtitle">,</span>
                    <Link to={"/fq/query"}></Link>
                </Modal>
            </Row>
        )
    }
}


export default Audit;


