import React , { Component } from 'react';
import { Row , Col , Table , Button } from 'antd';
import { Link } from 'react-router';

import Filter from '../../utils/Filter_mix'
import { page , order_status_map } from '../../ajax/config';
import axios from '../../ajax/request_form';
import { format_date } from '../../ajax/tool';
import { gtask_audit } from '../../ajax/api';

class Audit extends Component {
    constructor(props){
        super(props);
        this.state = {
            list:[]
        }
    }
    componentWillMount () {
        let status_color_map = {
            "-6" : "bg-danger", 
            "-5" : "bg-danger",
            "-4" : "bg-danger",
            "-3" : "bg-danger",
            "-2" : "bg-danger", 
            "-1" : "bg-danger",
            "0" : "bg-success",
            "1" : "bg-success",
            "2" : "", 
            "3" : "", 
            "4" : "", 
            "5" : "bg-success", 
            "6" : "", 
            "7" : "", 
            "8" : ""
        }
        this.columns = [
            {
                title:"",
                width:"80px",
                dataIndex:"orderId"
            },
            {
                title:"",
                dataIndex:"ownerName"
            },
            {
                title:"",
                dataIndex:"engineNo"
            },
            {
                title:"",
                dataIndex:"plateNo",
                render:(data)=>{
                    return data===null?'':data
                }
            },
            {
                title:"",
                dataIndex:"beneficiary"
            },
            {
                title:"",
                dataIndex:"phone"
            },
            {
                title:"",
                dataIndex:"clerk"
            },
            {
                title:"",
                render:(data)=>{
                    return format_date(data.createTime)
                }
            },
            {
                title:"",
                render:(data)=>{
                    return {
                        children:order_status_map[data.status],
                        props:{
                            className:status_color_map[data.status]
                        }
                    }
                }
            },
            {
                title:"",
                width:175,
                className:"text-left",
                render:(data)=>{
                    let operate = [];
                    if(data.status===2){
                        //operate.push(
                        //    <Link key="edit" to={"/fq/apply?orderNo="+data.orderId}><Button type="success" size="small"></Button></Link>
                        //)
                        operate.push(
                            <Link key="audit" to={"/fq/audit/detail?orderNo="+data.orderId+"&type=audit"}>&emsp;<Button type="primary" size="small"></Button></Link>
                        )
                    }
                    if(data.status===-2){
                        operate.push(<Link to={"/fq/audit/detail?orderNo="+data.orderId+"&type=failed"} key='detail1' ><Button size="small"></Button></Link>);
                        operate.push(
                            <Link key="audit" to={"/fq/apply?orderNo="+data.orderId}>&emsp;<Button type="primary" size="small"></Button></Link>
                        )
                    }
                    if(data.status===-6){
                        //operate.push(
                        //    <Link key="edit" to={"/fq/apply?orderNo="+data.orderId}><Button type="success" size="small"></Button></Link>
                        //)
                        operate.push(<Link to={"/fq/audit/detail?orderNo="+data.orderId+"&type=audit"} key='detail2'>&emsp;<Button size="small" type="primary"></Button></Link>);
                        //operate.push(
                        //    <Link key="bind" to={"/fq/apply?orderNo="+data.orderId+"&type=bind"}>&emsp;<Button type="primary" size="small"></Button></Link>
                        //)
                    }
                    return {
                        children:operate,
                        props:{
                            className:'text-left'
                        }
                    };
                }
            }
        ]
        
    }
    componentDidMount(){
        this.get_list();
    }
    search(values){
        this.setState({
            filter:values
        })
        this.get_list(1,values)

    }
    get_list(p=1,filter={}){
        let rqd = {
            page:p,
            page_size:page.size,
            ...filter
        }
        axios.post(gtask_audit,rqd).then(data=>{
            this.setState({
                list:data.data,
                current:data.current,
                total:data.total
            })
        })
    }
    page_up(page){
        this.get_list(page,this.state.filter);
    }
    render() {
        let pagination = {
            current:this.state.current,
            pageSize:page.size,
            total:this.state.total,
            onChange:this.page_up.bind(this)
        }
        let table_props = {
            columns : this.columns,
            className : "table-sh lg",
            pagination : pagination,
            rowKey:"orderId",
            dataSource : this.state.list
        }
        let filter_props = {
            "data-get":this.search.bind(this),
            "data-source":{
                condition:{
                    type:"mix",
                    placeholder:"///VIN"
                }
                // car_no:{
                //     title:"",
                //     type:"text",
                //     placeholder:""
                // },
                // power_no:{
                //     title:"",
                //     type:"text",
                //     placeholder:""
                // },
                // vin_code:{
                //     title:"vin",
                //     type:"text",
                //     placeholder:""
                // },
            }
        }

        return (
            <Col>
                <Row className="filter">
                    <Filter {...filter_props} />
                </Row>
                <Row>
                    <Table bordered {...table_props} />
                </Row>
            </Col>
        )
    }
}


export default Audit;


