import React , { Component } from 'react';
import { Row , Col , Table , Modal } from 'antd';
import { Link } from 'react-router';
import { browserHistory } from 'react-router';
import QRCode from 'qrcode.react';

// import { order_status_map } from '../../../ajax/config';
import axios from '../../../ajax/request_form';
import { get_payment_info , get_payment_plan , verify_pay_status } from '../../../ajax/api';

// const QRCode = require('qrcode.react');

class Audit extends Component {
    constructor(props){
        super(props);
        this.state = {
            downPaymentMoney:"",
            paidDownPaymentMoney:0,
            discountRate:"",
            syx:"",
            loanMoney:"",
            periods:"",
            repayDay:"",
            codeUrl:"",
            tbdSyxList:[],
            visible:false,
            success:false,
            auto_second:5,
            type:0,
            needRefund:""
        }
    }
    componentWillMount () {
        let pay_status = {
            "-1":"--",
            "0":"",
            "1":"",
            "2":"",
            "3":"",
        }
        this.columns = [
            {
                title:"",
                dataIndex:"period"
            },
            {
                title:"",
                render:data=>{
                    return data.repayDate||"--"
                }
            },
            {
                title:"",
                render:data=>{
                    return data.realRepayDate||"--"
                }
            },
            {
                title:"()",
                render:(data)=>{
                    return data.repayMoney.money()
                }
            },
            {
                title:"()",
                render:(data)=>{
                    return data.realRepayMoney.money()
                }
            },
            {
                title:"()",
                render:(data)=>{
                    return data.lateFee.money()
                }
            },
            {
                title:"",
                render:(data)=>{
                    return pay_status[data.status]
                }
            }
        ]
        
    }
    componentDidMount(){
        this.get_info();
        this.get_plan();
    }
    componentWillUnmount(){
        clearInterval(this.timer);
    }
    // 
    get_info(filter={}){
        let rqd = {
            orderId:this.props.location.query.orderNo
        }
        axios.post(get_payment_info,rqd).then(data=>{
            let res = data.data;
            this.setState({
                ...res
            })
            this.timer = setInterval((e)=>{
                this.verify_state()
            },2000);
        })
    }
    get_plan(){
        let rqd = {
            orderId:this.props.location.query.orderNo
        }
        axios.post(get_payment_plan,rqd).then(data=>{
            let res = data.data;
            this.setState({
                productName:res.productName,
                periods:res.periods,
                list:res.repayPlanDetailList
            })
        })
    }
    show(){
        this.setState({
            visible:true
        })
    }
    onOk(){

    }
    onCancel(){
        this.setState({
            visible:false
        })
    }
    // 
    verify_state(requestId){
        let rqd = {
            orderId:this.props.location.query.orderNo
        }
        axios.post(verify_pay_status,rqd).then(data=>{
            if(data.data){
                let auto_second = this.state.auto_second;
                clearInterval(this.timer);
                this.setState({
                    success:true
                });
                setInterval(e=>{
                    if(auto_second===0){
                        browserHistory.push("/fq/query");
                    }
                    this.setState({
                        auto_second:auto_second--
                    })
                    
                },1000);
            }
            
        })
    }
    render() {
        let table_props = {
            columns : this.columns,
            pagination : false,
            rowKey:"orderId",
            dataSource : this.state.list
        }
        let modal_props = {
            title:"",
            visible:this.state.visible,
            width:"60%",
            className:"pay-modal modal-long",
            footer:null,
            onOk:this.onOk.bind(this),
            onCancel:this.onCancel.bind(this)
        }
        let modal_result = {
            title:"",
            visible:true,
            className:"pay-modal",
            footer:null
        }
        let modal_success = {
            title:"",
            visible:this.state.success,
            className:"pay-modal",
            footer:null
        }
        // let test = {
        //     account:"",
        //     bank:"",
        //     no:"8110901411700617469"
        // }
        let qrimg = this.state.codeUrl?<QRCode size={150} value = {this.state.codeUrl} />:"";
        let pay_html = "";
        if(!this.state.needRefund){
            pay_html = (
                <Row>
                    <div className="page-subtitle"></div>
                    <Col span={12}>
                        { qrimg }
                        <div></div>
                    </Col>
                </Row>
            )
        }else{
            pay_html = (
                <Modal {...modal_result}>
                    <span className="page-subtitle">,</span>
                    <Link to={"/fq/query"}></Link>
                </Modal>
            )
        }
        let strs = [];
        for(let t in this.state.tbdSyxList){
            strs.push(this.state.tbdSyxList[t].money());
        }
        let payment_html = "";
        if(this.state.type===0){
            payment_html = (
                <div className="pay-content">
                    <div className="page-title"></div>
                    <div className="moneys">
                        <span className="money">
                            <span>{this.state.downPaymentMoney.money()}</span>
                        </span><br />
                        <span>={this.state.syx.money()}*{this.state.discountRate+"%"}</span><br />
                        <span>={strs.join("+")}</span>
                    </div>
                    <div className="info">
                        <span className="origin">{this.state.periods}</span> | {this.state.repayDay}&emsp;&emsp;&emsp;&emsp; 
                        <span className="origin">{this.state.loanMoney.money()}</span>
                        <a onClick={this.show.bind(this)}></a>
                    </div>
                </div>
            )
        }else{
            payment_html = (
                <div className="pay-content">
                    <div className="page-title"></div>
                    <div className="moneys">
                        <span className="money">
                            <span>{(this.state.downPaymentMoney-this.state.paidDownPaymentMoney).money()}</span>
                        </span><br />
                        <span>={this.state.syx.money()}*{this.state.discountRate+"%"}</span><br />
                        <span>={this.state.downPaymentMoney.money()}-{this.state.paidDownPaymentMoney.money()}</span>
                    </div>
                    <div className="info">
                        <span className="origin">{this.state.periods}</span> | {this.state.repayDay}&emsp;&emsp;&emsp;&emsp; 
                        <span className="origin">{this.state.loanMoney.money()}</span>
                        <a onClick={this.show.bind(this)}></a>
                    </div>
                </div>
            )
        }
        return (
            <Row className="content pay">
                <div className="page-title">
                    <Row>
                        <Col span={16}>,24,</Col>
                        <Col span={8} className="text-right"> <Link to={'/fq/query/detail?orderNo='+this.props.location.query.orderNo}></Link>&nbsp;</Col>
                    </Row>
                </div>
                { payment_html }
                <div className="pay-content">
                    <div className="page-title"></div>
                    { pay_html }<br />
                    {/*<Row>
                        <div className="page-subtitle">2</div>
                        <Col span={12}>
                            <TableCol data-source = {test} data-columns={this.columns}/>
                        </Col>
                    </Row>
                    <br />
                    <div><span className="origin"></span> +</div>*/}
                </div>
                <Modal {...modal_props}>
                    <span className="money-item">{this.state.loanMoney.money()}</span>
                    <span className="money-item">{this.state.downPaymentMoney.money()}</span>
                    <span className="money-item">{this.state.repayDay}</span>
                    <br /><br />
                    <Table bordered {...table_props} />
                </Modal>
                <Modal {...modal_success} >
                    <span className="page-subtitle">! {this.state.auto_second}s</span>
                    <Link to={"/fq/query"}></Link>
                </Modal>
            </Row>
        )
    }
}


export default Audit;


